@import url('https://fonts.googleapis.com/css?family=Roboto');
@mixin flex-row($flexNum:1) { 
  display: flex;
  flex: $flexNum;
  flex-direction: row;
 }
 @mixin backgroundImage($url) {
  background-image: url($url);
  @include bgi;
}

@mixin bgi {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
*{
  font-family: 'Roboto', sans-serif;
}
h2{
  margin: 0;
  margin-top: 20px;
  font-size: 2rem;
}
h3{
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
}
ul{
  margin:0;
  padding: 0;
  list-style: none;
  margin-left: 2rem;
}
.container{
  background-color: #fff;
  box-shadow: 0px 0px 5px #222;
  display: flex;
  flex-direction: column;
  
  width:70%;
  min-width: 320px;
  padding: 20px;
  @media screen and (max-width: 900px)  {
    flex-direction: row;
    flex-wrap: wrap;
  }
}
.subject{
  font-size: 30px;
}
.personInfoBox{
  @include flex-row(5);
  justify-content: center;  
  @media screen and (max-width: 900px)  {
    flex-direction: column;
    flex-wrap: wrap;
  }
  .personImage{
    // border:solid 1px green;
    display: flex;
    justify-content: center; 
    flex:1;  
    > .imageBox{
      width: 20vw;
      height: 20vw;
      overflow: hidden;
      box-shadow: 1px 1px 5px #222;
      border-radius: 50%;
      transform: rotate(90deg);
      @include backgroundImage("../assets/lai.JPG");
      @media screen and (max-width: 900px)  {
        width: calc(100vh/5);
        height:  calc(100vh/5);
      }
    }
  }
  .personWord{
    display: flex;
    justify-content: flex-start;  
    flex-direction: column;
    flex:4;
    padding-left: 10px;
    @media screen and (max-width: 900px)  {
      margin-top: 2rem;
    }
    > h2 {
      font-size: 36px;
    }
    > p {
      letter-spacing: 1px;
      line-height: 1.5rem;
      font-size: 1rem;
      font-weight: 500;
      font-family: sans-serif;
    }
    > .iconBox{
      margin-top: 10px;
    }
    > .professionalTitle{
      margin-top: 10px;
      font-weight: bolder;
    }
  }
}
.education{
  margin-top: 10px;
  .educationBox{
    margin-top: 10px;
    >ul{
      li{
        border-left: solid 3px  orange;
        padding-left:10px; 
        padding-top: 5px;
        font-size: 20px;
        letter-spacing: 1px;

      }
    }
  }
}
.skill{
  margin-top: 10px;
  hr{
    border: solid 1px plum;
  }
  h3{
      margin-top: 10px;
  }
}
.experience{
  margin-top: 10px;
  .experienceBox{
    margin-top: 10px;
    >ul{
      position: relative;
      .experienceTitle{
        font-size: 1.2rem;
        font-weight: bolder;
        color: darken(limegreen,15);
      }
      p{
        font-size: 1rem;
        font-weight: 500;
        
      }
      li{
        word-break: break-all;
        padding-left:20px;
        padding-bottom: 20px;
        border-left: solid 2px lightskyblue;
        &:before{
          content:'';
          display:block;
          width:28px;
          height:28px;
          background-color:lightskyblue;
          border-radius: 50%;
          position: absolute;
          left: calc(-28px/2 + 2px/2);
          
        }
        &:last-child {
          padding-bottom: 0px;
        }
      }
      
    }
  }
  
}